/* ===== Select Box ===== */
.sel {
  display: inline-block;
  background-color: transparent;
  position: relative;
  cursor: pointer;
  border-bottom: 1px solid #2b2b2b;
  background-image: url(../img/dropdown-arrow.svg);
  background-repeat: no-repeat;
  background-position: 0% 50%;
  width: 100%;
}

.sel__placeholder {
  font-family: Arimo;
  font-size: 16px;
  font-weight: 400;
  line-height: 34px;
  text-transform: uppercase;
  margin-top: 32px;
  pointer-events: none;
  color: #555;
  user-select: none;
  visibility: visible;
}

.cart-form__select .sel__placeholder {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 34px;
  text-transform: uppercase;
  width: 100%;
}

.cart-form__select .sel__box {
  padding-left: 3px;
  padding-right: 3px;
}

.cart-form__select .sel__box__options {
  border-bottom: 1px solid white;
  color: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 27px;
  text-transform: uppercase;
  background-color: black;
}

.cart-form__select .sel__box__options:last-child {
  border-bottom: 0;
}

.cart-form__select .sel__box__options:hover, .cart-form__select .sel__box__options:focus {
  color: #32c09d;
}

.sel.cart-form__commission-select {
  padding-right: 1px;
  padding-bottom: 0;
  margin-left: 1px;
  width: 42px;
}

.sel.cart-form__commission-select .sel__placeholder {
  color: #000000;
  font-family: Arimo;
  font-size: 14px;
  font-weight: 400;
  line-height: 12px;
  padding-bottom: 15px;
}

.sel.cart-form__commission-select .sel__placeholder:before {
  right: 5px;
}

.sel.cart-form__commission-select .sel__box {
  right: 0;
  max-height: 100px;
  overflow-y: scroll;
}

.sel.cart-form__commission-select .sel__box__options {
  border-bottom: 1px solid white;
  color: white;
  cursor: pointer;
}

.sel.cart-form__commission-select .sel__box__options:hover {
  color: #32c09d;
}

.sel__placeholder::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: attr(data-placeholder);
  visibility: hidden;
}

.sel__box {
  position: absolute;
  top: calc(100% + 4px);
  display: none;
  list-style-type: none;
  background-color: black;
  width: 100%;
  overflow: hidden;
}

.sel.active .sel__box {
  display: block;
}

.sel__box__options {
  display: list-item;
  user-select: none;
  color: #959494;
  font-size: 16px;
  font-weight: 400;
  line-height: 34px;
  text-transform: uppercase;
  border-bottom: 1px solid #424040;
  background-color: #2b2b2b;
  padding-right: 6px;
  padding-left: 6px;
}

@media only screen and (max-width: 480px) {
  .sel__box__options {
    font-size: 14px;
    line-height: 1.5;
  }
}

@media only screen and (max-width: 400px) {
  .sel__box__options {
    font-size: 16px;
    line-height: 2;
  }
}

/* ----- Select Box Black Panther ----- */
